"use client";

// 1. Import useEffect and additional icons
import { useState, useEffect } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import {
  CheckCircle2,
  XCircle,
  Volume2,
  RotateCcw,
  ChevronRight,
  ChevronLeft,
  Loader2, // For loading spinner
} from "lucide-react";
import VocabularyInfo from "@/components/VocabularyInfo";

// 2. Define the types for your data
// This type matches the frontend's hardcoded data structure
interface Vocabulary {
  title: string;
  description: string;
  videoUrl: string;
  imageUrl?: string | null; // Make optional, as API doesn't provide it
  partOfSpeech?: string; // Make optional
}

// This type matches the data from your Python backend
interface BackendVocabularyItem {
  id: number;
  word: string;
  description: string; // This comes from `instruction` in your query
  video_url: string; // This comes from `video` in your query
}

// This is the quiz question type
interface Question {
  id: number;
  videoUrl: string;
  correctAnswer: number; // index of the correct option
  options: {
    id: number;
    imageUrl: string;
    label: string;
  }[];
}

// This is the prop we expect from the parent (LessonTab)
interface Lesson {
  id: number;
  title: string;
}

// 3. REMOVE the hardcoded 'vocabularyList' and 'quizQuestions' arrays
// const vocabularyList: Vocabulary[] = [ ... ];
// const quizQuestions: Question[] = [ ... ];

// 4. Accept the 'lesson' prop
export default function LessonPlayer({ lesson }: { lesson: Lesson }) {
  // 5. Add state for fetched data, loading, and errors
  const [vocabularyList, setVocabularyList] = useState<Vocabulary[]>([]);
  const [quizQuestions, setQuizQuestions] = useState<Question[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const [phase, setPhase] = useState<"introduction" | "quiz">("introduction");
  const [currentVocabIndex, setCurrentVocabIndex] = useState(0);
  const [currentQuizIndex, setCurrentQuizIndex] = useState(0);
  const [quizAnswers, setQuizAnswers] = useState<(number | null)[]>([]); // Init empty
  const [showQuizResult, setShowQuizResult] = useState(false);
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null);

  // 6. Add useEffect to fetch data when the lesson.id changes
  useEffect(() => {
    if (!lesson.id) {
      setError("No lesson ID provided.");
      setIsLoading(false);
      return;
    }

    const fetchData = async () => {
      setIsLoading(true);
      setError(null);
      setPhase("introduction"); // Reset state for new lesson
      setCurrentVocabIndex(0);
      setCurrentQuizIndex(0);
      setSelectedAnswer(null);

      try {
        // Fetch vocabulary (using your API) and quiz in parallel
        // const [vocabResponse, quizResponse] = await Promise.all([
        //   fetch(
        //     `${process.env.NEXT_PUBLIC_SERVER_URL}/api/lessons/vocabulary?lesson_id=${lesson.id}`
        //   ),
        //   // We assume a similar endpoint exists for the quiz
        //   fetch(
        //     `${process.env.NEXT_PUBLIC_SERVER_URL}/api/lesson-quiz?lesson_id=${lesson.id}`
        //   ),
        // ]);
        const vocabResponse = await fetch(
          `${process.env.NEXT_PUBLIC_SERVER_URL}/api/lessons/vocabulary?lesson_id=${lesson.id}`
        );

        if (!vocabResponse.ok) throw new Error("Failed to fetch vocabulary.");
        // if (!quizResponse.ok)
        //   throw new Error("Failed to fetch quiz questions.");
        // Parse data
        const vocabData: { vocabulary: BackendVocabularyItem[] } =
          await vocabResponse.json();
        console.log(vocabData);
        // const quizData: { questions: Question[] } = await quizResponse.json();

        // ** DATA MAPPING **
        // Map your backend data to the frontend's expected format
        const mappedVocabulary: Vocabulary[] = vocabData.vocabulary.map(
          (item) => ({
            title: item.word, // Backend 'word' -> Frontend 'title'
            description: item.description, // Backend 'description' -> Frontend 'description'
            videoUrl: item.video_url, // Backend 'video_url' -> Frontend 'videoUrl'
            imageUrl: null, // API doesn't provide this, so set to null
          })
        );

        setVocabularyList(mappedVocabulary);
        setQuizQuestions(quizData.questions || []);

        // Initialize quiz answers array based on fetched data
        setQuizAnswers(new Array(quizData.questions?.length || 0).fill(null));
      } catch (err: any) {
        console.error(err);
        setError(err.message || "An unknown error occurred.");
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, [lesson.id]); // Re-run when the lesson changes

  // 7. Get current items (check if they exist)
  const currentVocab = vocabularyList[currentVocabIndex];
  const currentQuestion = quizQuestions[currentQuizIndex];

  const introProgress =
    vocabularyList.length > 0
      ? ((currentVocabIndex + 1) / vocabularyList.length) * 100
      : 0;
  const quizProgress =
    quizQuestions.length > 0
      ? ((currentQuizIndex + 1) / quizQuestions.length) * 100
      : 0;
  const currentProgress =
    phase === "introduction" ? introProgress : quizProgress;

  const isQuizCorrect =
    quizAnswers[currentQuizIndex] === currentQuestion?.correctAnswer;

  const handleFinishIntroduction = () => {
    // Check if there are questions to show
    if (quizQuestions.length > 0) {
      setPhase("quiz");
      setCurrentQuizIndex(0);
      setSelectedAnswer(null);
      setShowQuizResult(false);
    } else {
      // No quiz, just restart or finish
      handleRestart();
    }
  };

  // ... (rest of your handler functions: handleQuizAnswer, handleNextQuiz, etc. remain the same)
  const handleQuizAnswer = (optionIndex: number) => {
    setSelectedAnswer(optionIndex);
    const newAnswers = [...quizAnswers];
    newAnswers[currentQuizIndex] = optionIndex;
    setQuizAnswers(newAnswers);
    setShowQuizResult(true);
  };

  const handleNextQuiz = () => {
    if (currentQuizIndex < quizQuestions.length - 1) {
      setCurrentQuizIndex(currentQuizIndex + 1);
      setShowQuizResult(false);
      setSelectedAnswer(null);
    }
  };

  const handlePreviousQuiz = () => {
    if (currentQuizIndex > 0) {
      setCurrentQuizIndex(currentQuizIndex - 1);
      setShowQuizResult(false);
      setSelectedAnswer(null);
    }
  };

  const handleNextVocab = () => {
    if (currentVocabIndex < vocabularyList.length - 1) {
      setCurrentVocabIndex(currentVocabIndex + 1);
    }
  };

  const handlePreviousVocab = () => {
    if (currentVocabIndex > 0) {
      setCurrentVocabIndex(currentVocabIndex - 1);
    }
  };

  const handleRestart = () => {
    setPhase("introduction");
    setCurrentVocabIndex(0);
    setCurrentQuizIndex(0);
    setQuizAnswers(new Array(quizQuestions.length).fill(null));
    setShowQuizResult(false);
    setSelectedAnswer(null);
  };

  const correctCount = quizAnswers.filter(
    (ans, idx) => ans === quizQuestions[idx]?.correctAnswer
  ).length;

  // 8. Handle Loading state
  if (isLoading) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] text-gray-500">
        <Loader2 className="w-8 h-8 animate-spin text-[#f66868]" />
        <p className="mt-3 text-lg">ƒêang t·∫£i b√†i h·ªçc...</p>
      </div>
    );
  }

  // 10. Handle "No Vocabulary" state
  if (vocabularyList.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] text-gray-500">
        <p className="text-lg">Kh√¥ng t√¨m th·∫•y t·ª´ v·ª±ng n√†o cho b√†i h·ªçc n√†y.</p>
      </div>
    );
  }

  // 11. Your main component JSX (now safe to render)
  return (
    <div className="space-y-8 max-w-4xl mx-auto px-4 py-8">
      {/* HEADER */}
      <div className="text-center">
        <h2 className="text-4xl font-extrabold mb-3 bg-gradient-to-r from-[#f66868] to-rose-400 bg-clip-text text-transparent">
          {phase === "introduction"
            ? "H·ªçc T·ª´ V·ª±ng K√≠ Hi·ªáu"
            : "Ki·ªÉm Tra Ki·∫øn Th·ª©c"}
        </h2>
        <p className="text-muted-foreground text-sm">
          {phase === "introduction"
            ? `T·ª´ v·ª±ng ${currentVocabIndex + 1} / ${vocabularyList.length}`
            : `C√¢u h·ªèi ${currentQuizIndex + 1} / ${quizQuestions.length}`}
        </p>
      </div>
      {/* PROGRESS */}
      <div className="space-y-1">
        <div className="flex justify-between text-sm font-medium">
          <span className="text-foreground">Ti·∫øn ƒë·ªô</span>
          <span className="text-[#f66868]">{Math.round(currentProgress)}%</span>
        </div>
        <Progress
          value={currentProgress}
          className="h-2 bg-rose-100 [&>div]:bg-[#f66868]"
        />
      </div>
      <div className="w-full flex justify-center">
        <div className="mt-1 w-fit rounded-xl border border-rose-200 bg-gradient-to-r from-rose-50 to-rose-100 p-4 text-center shadow-sm flex flex-row gap-x-4 items-center">
          <div className="text-xl">üí°</div>
          <div className="flex flex-col gap-y-1">
            <p className="text-rose-700 font-medium text-lg">
              M·∫πo nh·ªè: H√£y b·∫≠t camera ƒë·ªÉ t·ª± ki·ªÉm tra b·∫£n th√¢n nh√©!
            </p>
          </div>
        </div>
      </div>
      {/* === PHASE: INTRODUCTION === */}
      {phase === "introduction" && currentVocab && (
        <VocabularyInfo
          word={currentVocab.title}
          partOfSpeech={currentVocab.partOfSpeech}
          definition={currentVocab.description}
          videoUrl={currentVocab.videoUrl}
          imageUrl={currentVocab.imageUrl || undefined} // Pass undefined if null
        />
      )}
      {/* === PHASE: QUIZ === */}
      {phase === "quiz" && currentQuestion && (
        <Card className="overflow-hidden border-2 shadow-sm hover:shadow-md transition-all scale-[0.98]">
          <CardHeader className="border-b-2 py-2 px-4">
            <div className="flex justify-between items-start">
              <div>
                <CardTitle className="text-lg font-semibold text-foreground">
                  K√≠ Hi·ªáu N√†y L√† G√¨?
                </CardTitle>
                <CardDescription className="text-xs mt-0.5">
                  Xem video v√† ch·ªçn ƒë√°p √°n ƒë√∫ng
                </CardDescription>
              </div>
              <Badge className="bg-[#f66868]/10 text-[#f66868] border border-[#f66868]/30 text-[11px] py-0.5 px-2">
                Ki·ªÉm Tra
              </Badge>
            </div>
          </CardHeader>
          <CardContent className="pt-4 space-y-4 px-4 pb-4">
            {/* VIDEO (Make sure currentQuestion.videoUrl is valid) */}
            <div className="relative rounded-lg overflow-hidden aspect-video bg-rose-50 shadow-inner">
              <img
                src={currentQuestion.videoUrl}
                alt="C√¢u h·ªèi"
                className="w-full h-full object-cover"
              />
              <div className="absolute inset-0 flex items-center justify-center bg-black/20">
                <button className="w-12 h-12 rounded-full flex items-center justify-center bg-[#f66868] hover:bg-[#f66868]/90 transition">
                  <Volume2 className="text-white w-6 h-6" />
                </button>
              </div>
            </div>
            {/* OPTIONS */}
            <div className="grid grid-cols-2 gap-3">
              {currentQuestion.options.map((option, index) => (
                <button
                  key={option.id}
                  onClick={() => handleQuizAnswer(index)}
                  disabled={showQuizResult}
                  className={`group rounded-lg overflow-hidden border transition-all duration-300 ${
                    selectedAnswer === index
                      ? isQuizCorrect
                        ? "border-green-500 ring-1 ring-green-300"
                        : "border-red-500 ring-1 ring-red-300"
                      : "hover:border-[#f66868]/70"
                  }`}
                >
                  <div className="relative aspect-square">
                    <img
                      src={option.imageUrl}
                      alt={option.label}
                      className="w-full h-full object-cover group-hover:scale-105 transition-transform"
                    />
                    {selectedAnswer === index && showQuizResult && (
                      <div
                        className={`absolute inset-0 flex items-center justify-center ${
                          isQuizCorrect ? "bg-green-500/20" : "bg-red-500/20"
                        }`}
                      >
                        {isQuizCorrect ? (
                          <CheckCircle2 className="w-8 h-8 text-green-600" />
                        ) : (
                          <XCircle className="w-8 h-8 text-red-600" />
                        )}
                      </div>
                    )}
                  </div>
                  <div className="p-1.5 bg-white border-t text-xs font-medium text-center">
                    {option.label}
                  </div>
                </button>
              ))}
            </div>
            {/* RESULT */}
            {showQuizResult && (
              <div
                className={`p-3 rounded-md border text-sm ${
                  isQuizCorrect
                    ? "bg-green-50 border-green-200 text-green-800"
                    : "bg-red-50 border-red-200 text-red-800"
                }`}
              >
                <p className="font-semibold mb-0.5">
                  {isQuizCorrect ? "Tuy·ªát v·ªùi!" : "Ch∆∞a ƒë√∫ng"}
                </p>
                <p className="text-xs leading-snug">
                  {isQuizCorrect
                    ? `ƒê√∫ng r·ªìi! ƒê√¢y l√† k√≠ hi·ªáu cho "${
                        currentQuestion.options[currentQuestion.correctAnswer]
                          .label
                      }".`
                    : `ƒê√°p √°n ƒë√∫ng l√† "${
                        currentQuestion.options[currentQuestion.correctAnswer]
                          .label
                      }". H√£y th·ª≠ l·∫°i nh√©!`}
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      )}
      {/* NAVIGATION */}
      <div className="flex items-center justify-between">
        <Button
          variant="outline"
          onClick={
            phase === "introduction" ? handlePreviousVocab : handlePreviousQuiz
          }
          disabled={
            phase === "introduction"
              ? currentVocabIndex === 0
              : currentQuizIndex === 0
          }
          className="flex items-center gap-2 border-rose-200 text-[#f66868] hover:bg-rose-50"
        >
          <ChevronLeft className="h-4 w-4" /> Quay L·∫°i
        </Button>
        <div className="flex items-center gap-2">
          {(phase === "introduction" ? vocabularyList : quizQuestions).map(
            (_, idx) => (
              <div
                key={idx}
                className={`h-2 rounded-full transition-all ${
                  (phase === "introduction"
                    ? currentVocabIndex
                    : currentQuizIndex) === idx
                    ? "w-8 bg-[#f66868]"
                    : "w-2 bg-muted"
                }`}
              />
            )
          )}
        </div>
        <Button
          onClick={
            phase === "introduction"
              ? currentVocabIndex === vocabularyList.length - 1
                ? handleFinishIntroduction
                : handleNextVocab
              : currentQuizIndex === quizQuestions.length - 1
              ? handleRestart
              : handleNextQuiz
          }
          disabled={phase === "quiz" && !showQuizResult}
          className="flex items-center gap-2 bg-[#f66868] hover:bg-[#f66868]/90 text-white shadow-md"
        >
          {phase === "quiz" && currentQuizIndex === quizQuestions.length - 1 ? (
            <>
              <RotateCcw className="h-4 w-4" /> B·∫Øt ƒê·∫ßu L·∫°i
            </>
          ) : (
            <>
              Ti·∫øp T·ª•c <ChevronRight className="h-4 w-4" />
            </>
          )}
        </Button>
      </div>
    </div>
  );
}
